import React from "react";

export default class InfoGeneral extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            surCampus: null,
            dataGeneral: {
                dateDepot: null,
                dateDebutDeManif: null,
                heureDebutDeManif: null,
                dateFinDeManif: null,
                heureFinDeManif: null,
                associationOrganisatrice: null,
                nomManif: null,
                presidentAssoNom: null,
                presidentAssoPrenom: null,
                presidentAssoTel: null,
                presidentAssoMail: null,
                contactSpecifiqueNom: null,
                contactSpecifiquePrenom: null,
                contactSpecifiqueTel: null,
                contactSpecifiqueMail: null,
                descriptionManif: null,
                etatDesLieux: null,
            },
        }

        this.styleOuiNon_actif = {
            backgroundColor: "#13435b",
            color: "white",
            borderStyle: "double",
            borderWidth: "0.3em",
            borderColor: "white",
        }
        this.styleOuiNon_inactif = {
            backgroundColor: "white",
            color: "#13435b",
            borderStyle: "solid",
            borderWidth: "0.05em",
            borderColor: "#13435b",
        }

        this.handleChange = this.handleChange.bind(this)
        this.surCampusOui = this.surCampusOui.bind(this)
        this.surCampusNon = this.surCampusNon.bind(this)
    }

    handleChange(event) {
        if (event.target.name in this.state.dataGeneral) {
            let newDataGeneral = this.state.dataGeneral;
            newDataGeneral[event.target.name] = event.target.value;
            this.setState({dataGeneral: newDataGeneral})
            this.props.onChange(newDataGeneral);
        }
        //console.log(event.target.name);
        //console.log(this.state.dataGeneral);
        //console.log(this.state.surCampus);
    }

    surCampusOui () {
        this.setState({surCampus: true});
        let PLPSurCampus = document.getElementById("PLP-SurCampus")
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus")
        for (let s in this.styleOuiNon_actif){PLPSurCampus.style[s] = this.styleOuiNon_actif[s]}
        for (let s in this.styleOuiNon_inactif){PLPHorsCampus.style[s] = this.styleOuiNon_inactif[s]}
        this.props.onChange("surCampusOui");
    }
    surCampusNon () {
        this.setState({surCampus: false});
        let PLPSurCampus = document.getElementById("PLP-SurCampus")
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus")
        for (let s in this.styleOuiNon_actif){PLPHorsCampus.style[s] = this.styleOuiNon_actif[s]}
        for (let s in this.styleOuiNon_inactif){PLPSurCampus.style[s] = this.styleOuiNon_inactif[s]}
        this.props.onChange("surCampusNon");
    }

    render() {
        return(
            <div id={"InfoGeneral-container"}>
                <div id={"PLP-surCampusOuNon"}>
                    <div className={"PLP-questions"} id={"PLP-surCampus-question"}> Où se déroule votre événement ? </div>
                    <div id={"PLP-SurCampus"} onClick={() => {this.surCampusOui()}}> SUR CAMPUS </div>
                    <div id={"PLP-HorsCampus"} onClick={() => {this.surCampusNon()}}> HORS CAMPUS </div>
                </div>
                <div id={"PLP-Dates"}>
                    <div className={"PLP-Dates-containers"}>
                        <div className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date de dépôt de la demande : </div>
                        <input className={"PLP-retours"} name={"dateDepot"} value={this.state.dataGeneral.dateDepot} onChange={this.handleChange}/>
                    </div>
                    <div className={"PLP-Dates-containers"}>
                        <div className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date et heure de début de la manifestation : </div>
                        <input
                            className={"PLP-retours"}
                            name={"dateDebutDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.dateDebutDeManif}
                        />
                        <input
                            className={"PLP-retours"}
                            name={"heureDebutDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.heureDebutDeManif}
                        />
                    </div>
                    <div className={"PLP-Dates-containers"}>
                        <div className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date et heure de fin de la manifestation : </div>
                        <input
                            className={"PLP-retours"}
                            name={"dateFinDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.dateFinDeManif}
                        />
                        <input
                            className={"PLP-retours"}
                            name={"heureFinDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.heureFinDeManif}
                        />
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        let PLPSurCampus = document.getElementById("PLP-SurCampus");
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus");
        for (let s in this.styleOuiNon_inactif){
            PLPSurCampus.style[s] = this.styleOuiNon_inactif[s];
            PLPHorsCampus.style[s] = this.styleOuiNon_inactif[s];
        }

    }
}